.spinner {
    position: absolute;
    display: flex;
    width: 50px;
    height: 50px;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);

    svg {
        animation: spinner 1.5s infinite linear;
    }
}

@keyframes spinner {
    100% {
        transform: rotate(-360deg)
    }
}